import COS from 'cos-js-sdk-v5'
import axios from '@/utils/request'

// 初始化实例
var cos = new COS({
  getAuthorization: function (options, callbackfun) {
    // 异步获取临时密钥
    getCosData().then((res) => {
      const data = res.access_info
      const credentials = data.credentials
      callbackfun({
        TmpSecretId: credentials.tmp_secret_id,
        TmpSecretKey: credentials.tmp_secret_key,
        XCosSecurityToken: credentials.session_token, // 建议返回服务器时间作为签名的开始时间，避免用户浏览器本地时间偏差过大导致签名错误
        StartTime: data.start_time, // 时间戳，单位秒，如：1580000000
        ExpiredTime: data.expired_time // 时间戳，单位秒，如：1580000900
      })
    })
  }
})

export default cos

function getCosData () {
  return new Promise((resolve, reject) => {
    axios.get('/v1/cloud/cos/access').then((res) => {
      if (res && res.data) {
        resolve(res.data)
      }
    })
  })
}
