<template>
  <van-uploader class="upload-wraper"
      v-model="fileList"
      :max-size="10 * 1024 * 1024"
      multiple
      @oversize="onOversize"
      @delete="deleteImg"
      :before-read="beforeRead"
      :after-read="afterRead"
      :max-count="9"
      upload-icon="plus" />
</template>

<script>
import { Uploader, Toast } from 'vant'
import cos from '@/utils/cos.js'
// import { Uploader, Toast } from 'vant'
export default {
  name: 'UploadImg',
  components: {
    'van-uploader': Uploader
  },
  computed: {

  },
  data () {
    return {
      fileList: []
    }
  },
  created () {
  },
  methods: {
    deleteImg (file) {
      this.$emit('getImgList', this.fileList.map(item => item.url))
    },
    onOversize (file) {
      console.log(file)
      Toast({
        message: '文件大小不能超过 10M'
      })
    },
    checkImgType (file) {
      const typeInfo = file.type.split('/')
      const imgType = typeInfo[typeInfo.length - 1].toLowerCase()
      const typeArr = ['jpg', 'png', 'gif', 'jpeg']
      if (typeArr.indexOf(imgType) === -1) {
        Toast('您上传了错误格式的图片')
        return false
      }
      return true
    },
    beforeRead (file) {
      // file单文件是对象， 多文件上传是数组
      let status = false
      if (file && file.length) {
        for (let i = 0; i < file.length; i++) {
          status = this.checkImgType(file[i])
        }
      } else {
        status = this.checkImgType(file)
      }
      return status
    },
    dealImgStatus (file) {
      const fileType = file.file.type.split('/')[1]
      file.status = 'uploading'
      file.message = '上传中...'
      cos.putObject({
        Bucket: 'fpv-1251316249', // 存储桶名称
        Region: 'ap-shanghai', /* 存储桶所在地域，必须字段 */
        Key: `fpv/${Date.now()}.${fileType}`, // 必须 -- 文件名称随机
        StorageClass: 'STANDARD',
        Body: file.file, // 上传文件对象
        onProgress: function (progressData) {
          console.log(JSON.stringify(progressData))
        }
      }, (err, data) => {
        if (err) {
          file.status = 'failed'
          file.message = '上传失败'
        } else {
          file.status = 'done'
          file.message = '上传完成'
          file.url = data.Location
          this.$emit('getImgList', this.fileList.map(item => item.url))
        }
        console.log('res---------', err || data)
      })
    },
    afterRead (file) {
      if (file && file.length) { // 多张图片同时上传
        for (let i = 0; i < file.length; i++) {
          this.dealImgStatus(file[i])
        }
      } else {
        this.dealImgStatus(file)
      }
    }
  }
}
</script>

<style lang="less" scoped>
.up-load-wrap {

}
</style>
