<template>
  <div class="problem-feedback-wrap page">
    <van-radio-group class="checkbox-group" v-model="subData.problem" direction="horizontal">
      <van-radio v-for="item in checkListData" :key="item.value"
        class="checkbox-item"
        icon-size="0.37rem"
        :name="item.value"
      >
        {{ item.text }}
      </van-radio>
    </van-radio-group>

    <van-field
      v-model="subData.describe"
      rows="6"
      autosize
      type="textarea"
      maxlength="500"
      placeholder="请您尽可能详细描述问题，我们会尽快跟进~"
      show-word-limit
    />

    <upload-img class="upload-wraper " @getImgList="getImgList" />

    <van-button class="sub-btn" type="primary" :disabled="isDisabled" round size="large" @click="subDatas">提交</van-button>
  </div>
</template>

<script>
import UploadImg from '@/components/UploadImg'
import { RadioGroup, Radio, Field, Button, Toast } from 'vant'
export default {
  components: {
    'van-radio-group': RadioGroup,
    'van-radio': Radio,
    'van-field': Field,
    'van-button': Button,
    UploadImg
  },
  computed: {
    isDisabled () {
      return !this.subData.problem
    }
  },
  data () {
    return {
      subData: {
        describe: '',
        phone: '13636619628',
        pics: '',
        problem: ''
      },
      message: '',
      imgList: [],
      checkListData: [
        { value: '登录/注册失败', text: '登录/注册失败' },
        { value: '视频播放失败', text: '视频播放失败' },
        { value: '评论/回复', text: '评论/回复' },
        { value: '视频内容', text: '视频内容' },
        { value: '个人中心', text: '个人中心' },
        { value: '账号相关', text: '账号相关' },
        { value: '其它问题（请您描述)', text: '其它问题（请您描述）' }
      ]
    }
  },
  created () {
    console.log(this.$nativeBridge)
  },
  methods: {
    subDatas () {
      this.subData.pics = this.imgList.join(',')
      console.log('this.subData-------------', this.subData)
      this.$axios.post('/user/feedback', this.subData).then((res) => {
        Toast('提交成功')
      }).catch((e) => {
        console.log(e)
      })
    },
    getImgList (e) {
      this.imgList = e
    }
  }
}
</script>

